body{
  background-color: #6bcaa6;
  color: #224a7e;
}
button{
  height: 70px;
  width: 70px;
  background-color: #224a7e;
  border-radius: 30%;
  margin: 2px;
  box-shadow: 0px 0px 0px transparent;
    border: 0px solid transparent;
    text-shadow: 0px 0px 0px transparent;

    font-size: 1.5rem;
    font-family: 'Chelsea Market', cursive;
    color: #ebdb93;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease 0s;
}
h1{
  margin: 20px 0px ;
}
 
.game{
  margin: 0 auto;
  text-align: center;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  font-size: 1.5rem;
  font-family: 'Chelsea Market', cursive;
}

.board{
  
  border-radius: 10%;
  padding: 10px;
  margin: 0px auto;
  display: block;
  margin: 0px auto;
   
} 

.play-again{
  width: 210px;
  height: 70px;
  border-radius: 5%;
  margin: 20px 0px;
  font-size: 1.5rem;
}

.status{
  margin:20px;
}

.square{
